
/*一 、 @import 我们的css从此可以进行不短的拆分确保我们的程序可维护性变高*/
@import "./reset";

/*所有的sass之中的关键字 都以 @开头*/

/* 二 、变量 

      sass 提供了很多的工具函数 : 
      darken(); 黑暗;
      ligthen(); 变量;
*/
@import "./variable";

body{
      background: $base-color;
}
#box{
      width:100px;
      height: 100px;
      // background: #ddd - 100;
      background: darken($base-color,50);
      color: lighten($base-color , 10);
}

/*
   三、数组 
*/

.btn{
      background:  #ddd;
      color: #fff;
      border-radius: 10px;
      border:none;
}

