// 提取;
@import "./mixin";

#box{
      @include abs-center;
}

.dox{
      width: 200px;
      height: 100px;
      background: antiquewhite;
     @include  left-abs-center(200px);
     @include c3p(transform,rotate(60deg) scale(1.5));
}

// 七 继承 ;


.cox{
      @extend .dox;

      background: #fff;
      @include  left-abs-center(800px);
}

